        methods: {

            EditLine () {
                this.isLoading = true
                this.axios.post('/api/programmesrondes/' + this.form.id + '/update', this.form).then(response => {
                    this.isLoading = false
                    this.gridApi.applyServerSideTransaction({
                        update: [
                            response.data
                        ],
                    });
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    this.errors = error.response.data.errors
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de l\'enregistrement')
                })
            },
            DeleteLine () {
                this.isLoading = true
                this.axios.post('/api/programmesrondes/' + this.form.id + '/delete').then(response => {
                    this.isLoading = false

                    this.gridApi.applyServerSideTransaction({
                        remove: [
                            this.form
                        ]
                    });
                    this.gridApi.refreshServerSide()
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
